import React, { Fragment } from "react";
import { Button } from "antd";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";
import { PublishedFolder, Feature, AccessControlTypes } from "types";
import BuyLicenseAlertBox from "../ui/BuyLicenseAlertBox";
import useAppStore from "components/context/app/Hooks";

export default function FolderModal() {
  const { licensed } = useAppStore();
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/publishedfolder", "create", {
      id: 0,
    } as PublishedFolder);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator", "Operator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
        <Button children="Create New Publish Folder" />
      </ModalOpenButton>
      <ModalContent title="Create new publish folder" onClickOk={handleOk} formName="create_folder_form">
        <Form schemaName="publishedfolder">
          <FormContentBase
            name="create_folder_form"
            form={form}
            preserve={false}
            defaultValues={{
              path: "",
              requestPath: "",
              defaultDocument: null,
              authentication: licensed(Feature.Dashboard),
              role: [],
            }}
          />
          {!licensed(Feature.Dashboard) && <BuyLicenseAlertBox />}
        </Form>
      </ModalContent>
    </Fragment>
  );
}
